import { _decorator, Component, JsonAsset, Node, resources, SpriteFrame, SpriteAtlas, instantiate } from 'cc';
const { ccclass, property } = _decorator;
import { ChipEntry } from '../Chip/ChipEntry';
import { EventManager } from './EventManager';
import { GameEvent } from '../enums/GameEvent';

@ccclass('BetManager')
export class BetManager extends Component {
    @property({ type: [ChipEntry] })
    chips: ChipEntry[] = [];

    @property()
    private maxBet;

    private selectedChips: ChipEntry[] = [];
    private currentBet;

    public addChip(chip: ChipEntry): boolean {
        if (this.currentBet + chip.value > this.maxBet) {
            console.warn("Max bet reached");
            return false;
        }

        this.selectedChips.push(chip);
        this.currentBet += chip.value;
        return true;
    }

    public clearBet() {
        this.selectedChips = [];
        this.currentBet = 0;
    }

    public getTotalBet(): number {
        return this.currentBet;
    }

    public getSelectedChips(): ChipEntry[] {
        return [...this.selectedChips];
    }

}

